import './App.css';
import { Button, Layout, Row, Modal} from 'antd';
import { QrcodeOutlined } from '@ant-design/icons';
import LectorQR from './components/LectorQR';
import { useState } from 'react';
import AppContext from './AppContext';


const { Header, Content } = Layout;
function App() {

  const [open, setOpen] = useState(false);
  const [qrInfo, setQrInfo] = useState([]);

  console.log(qrInfo)
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div className="App">
      <AppContext.Provider value={{ qrInfo, setQrInfo}}>
      <Layout>
        <Header> <h1 style={{ color: 'white' }}>Lector de codigos QR del SAT</h1></Header>
        <Content>
          <Row>
            <Button type="primary" icon={<QrcodeOutlined />} onClick={() => handleClickOpen()}>
              Escanear código QR
            </Button>
          </Row>
          <Modal
            title="Escanear código QR "
            centered
            open={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            width={'90vw'} 
          >
            <LectorQR></LectorQR>
          </Modal>

          {qrInfo.map((item) =>
            <li key={item.key}>
              {item.key} {item.value}
            </li>
          )}
        </Content>
      </Layout>
      </AppContext.Provider>
    </div>
  );
}

export default App;
