
import React, { useState, useEffect, useContext } from 'react';
import { QrReader } from 'react-qr-reader';
import axios from 'axios';
import cheerio from "cheerio";
import AppContext from '../AppContext';


const LectorQR = (props) => {
  const [data, setData] = useState('No result');
  const { qrInfo, setQrInfo } = useContext(AppContext);

  const urlInicial = "https://siat.sat.gob.mx/app/qr/faces/pages/mobile/validadorqr.jsf?";
  useEffect(() => {
    // Update the document title using the browser API
    async function fetch() {
      const response = await axios.get(`https://siat.sat.gob.mx/app/qr/faces/pages/mobile/validadorqr.jsf?D1=10&D2=1&D3=15030130252_SOGM8004154F7`);
      const html = response.data;
      const $ = cheerio.load(html);
      let rfc = "";
      //let curp = "";
      //let nombre = "";
      //let apPaterno = "";
      //let apMaterno = "";
      //let cp = "";
      $("ul li").each(function (i, element) {
        if (i === 0) {
          rfc = $(element).text().substring(7, 21);
        }
      });

      var kv = [];
      $(".ui-widget-content tr ").each(function (i, el) {
        let selector = $('td', el);
        console.log(selector)
        if (selector.length === 2) {
          kv.push({ key: $(selector[0]).text(), value: $(selector[1]).text() });
        }

      });
      setQrInfo(kv);
    }
  }, []);
  return (
    <div>
      <QrReader
        constraints={{
          facingMode: 'environment'
        }}
        onResult={async (result, error) => {
          if (!!result) {
            const url = result?.text;
            if (url.startsWith(urlInicial)) {
              setData(result?.text);
              const response = await axios.get(url);
              const html = response.data;
              const $ = cheerio.load(html);
              let rfc = "";
              //let curp = "";
              //let nombre = "";
              //let apPaterno = "";
              //let apMaterno = "";
              //let cp = "";
              $("ul li").each(function (i, element) {
                if (i === 0) {
                  rfc = $(element).text().substring(7, 21);
                }
              });

              var kv = [];
              $(".ui-widget-content tr ").each(function (i, el) {
                let selector = $('td', el);
                console.log(selector)
                if (selector.length === 2) {
                  kv.push({ key: $(selector[0]).text(), value: $(selector[1]).text() });
                }

              });
              setQrInfo(kv);
            } else {
              alert("URL no corresponde");
              console.info("URL no corrresponde");
            }

          }

          if (!!error) {
            console.info(error);
          }
        }}
        style={{ width: '100vw', height: '100vw' }}
      />
      <p>{data}</p>
    </div>
  );
};

export default LectorQR;